.contact_us {
  padding-top: 100px;
  background: var(--color-white) url("../img/contact-us/bg-img.png") 50%/cover no-repeat;
}

@media screen and (max-width: 576px) {
  .contact_us {
    padding-top: 15px;
  }
}

.contact_us__content {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.contact_us__title {
  white-space: nowrap;
}

@media screen and (max-width: 576px) {
  .contact_us__title {
    white-space: normal;
  }
}

.contact_us .form {
  background: var(--color-light-blue);
}

.contact_us__logo {
  margin-top: 100px;
}

@media screen and (max-width: 768px) {
  .contact_us__logo {
    margin-top: 50px;
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 540px) {
  .contact_us__logo {
    margin-top: 15px;
  }
}

.contact_us__container {
  padding-top: 225px;
  padding-bottom: 300px;
}

@media screen and (max-width: 768px) {
  .contact_us__container {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.footer {
  background: var(--color-blue);
  color: var(--color-white);
}

.footer__copyright-container {
  padding-top: 30px;
  padding-bottom: 30px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

@media screen and (max-width: 576px) {
  .footer__copyright-container {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.footer__text-copyright {
  font-family: var(--font-pier-nexa-book);
  font-size: 12px;
  line-height: 16px;
  text-transform: lowercase;
  color: var(--color-white);
}

@media screen and (max-width: 576px) {
  .footer__menu {
    justify-content: center;
  }
  .footer__list {
    display: block;
    width: 100%;
  }
  .footer__list .header__menu-item {
    margin-left: 0;
  }
  .footer__list .header__menu-item:first-child {
    margin-left: 0;
  }
}
